import React, { Component } from 'react'
import { Layout, Row, Col } from "antd";
import { Fade } from 'react-reveal';
import { scroller } from "react-scroll";
import { DesktopMenu, MobileMenu } from "./";

class Header extends Component {
  state = {
    fixed: false
  }

  componentDidMount() {
    window.addEventListener('scroll', this.onScroll);
  }

  onScroll = (e) => {
    let top;
    const { fixed } = this.state
    top = (document.documentElement ? document.documentElement.scrollTop : null) || (document.scrollingElement ? document.scrollingElement.scrollTop : null) || 0;
    if (top > 32) {
      document.getElementById("navbar").classList.contains('fixed') ? null : document.getElementById("navbar").classList.add('fixed');
      if (!fixed) this.setState({ fixed: true })
    }
    else {
      document.getElementById("navbar").classList.contains('fixed') ? document.getElementById("navbar").classList.remove('fixed') : null;
      if (fixed) this.setState({ fixed: false })
    }
  };

  render() {
    const { fixed } = this.state;
    return (
      <Layout.Header className={`${fixed ? 'fixed' : ''} container-fluid`} ref={el => this.navbar = el} id="navbar" >
        <Row type="flex" justify="space-between">
          <Col xs={23} md={5}>
            <Fade top>
              <img className="logo-brand" src={`/static/img/${fixed ? 'Logo-alpha' : 'Logo'}.png`} />
            </Fade>
          </Col>
          <Col xs={1} md={19} className="text-right">
            <Fade top>
              {/* <DesktopMenu /> */}
              <MobileMenu />
            </Fade>
          </Col>
        </Row>
      </Layout.Header >
    )
  }
}

export default Header;
